@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,100&family=Source+Code+Pro:wght@300&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  background-color: #0b0c10;
  color: azure;
}

html {
  --dark: #2d033b;
  --dark1: #1f2833;
  --main: #810ca8;
  --main4: #892cdc;
  --main2: #c147e9;
  --main3: #e5b8f4;
  --white: #ffffff;
  --cream: #c5c6c7;
}

body::-webkit-scrollbar {
  width: 15px;
}

body::-webkit-scrollbar-track {
  background: var(--scrollbarBG);
}
::-webkit-scrollbar {
  width: 120px;
}
body::-webkit-scrollbar-thumb:hover {
  background: var(--main);
}
body::-webkit-scrollbar-thumb {
  background-color: var(--main2);
  border-radius: 6px;
  border: 3px solid var(--scrollbarBG);
}
