/* SCREENS STYLING */
html{
    --dark:#2D033B;
    --dark1:#1F2833;
    --main:#810CA8;
    --main4:#892CDC;
    --main2:#C147E9;
    --main3:#E5B8F4;
    --white: #ffffff;   
    --cream:#C5C6C7;
}
th,td{
    font-weight: bold;
    color: #fff;
    border:4px solid #FFF ;
    padding: 10px 50px;
    transition: 1s;
}

td:hover,th:hover{
    background-color: var(--dark1);
    color:var(--main2);
    
    
}
table{
    margin: 10px auto;
}
.scaleclass{
    transition: 1s;
}
.scaleclass:hover,.instagram:hover{
    transform: scale(1.2);
    
}

.gradient{
    background-color: #38a4e2;
background-image: linear-gradient(135deg, #38a4e2 0%, #9599E2 100%);

}
.instagram{
   background:  radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
   transition: 1s;
}
.linked{
    background:  #0A66C2;
}